.wrapper {
  width: 100%;
  height: 100%;
  padding: 3px;
  background-color: #fff;
  display: flex;
  justify-content: flex-start;
  align-content: flex-start;
  flex-wrap: wrap;
  flex-direction: row;
  overflow-y: auto;
  transition: background-color 0.25s ease-in-out;
}

.cardOver {
  background-color: rgb(204, 255, 255);
}
