.wrapper {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  z-index: 900;
}

.box {
  min-width: 300px;
  max-width: calc(100vw - 10px);
  max-height: calc(100vh - 10px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  border-radius: 5px;
  background-color: #fff;
  color: #000;
  box-shadow: 4px 8px 6px #ccc;
}

.title {
  font-size: 20px;
  margin-bottom: 12px;
}

.message {
  font-size: 12px;
}

.buttonsContainer {
  margin-top: 24px;
  display: flex;
  flex-direction: row;
}
