.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 8px;
}

.title {
  overflow: hidden;
  width: 200px;
  max-width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 12px;
  color: #000;
}

.iconWrapper {
  margin: 0 1px;
  width: 26px;
  height: 26px;
  padding: 1px;
  flex-shrink: 0;
}

.iconWrapper:hover {
  padding: 0;
}

.iconWrapper:active {
  padding: 2px;
}

.icon {
  cursor: pointer;
  width: 100%;
  height: 100%;
}
