.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 8px;
}

.title {
  margin-left: 8px;
  overflow: hidden;
  width: 200px;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 12px;
  color: #000;
}

.iconWrapper {
  margin: 0 1px;
  width: 26px;
  height: 26px;
  padding: 1px;
  flex-shrink: 0;
}

.iconWrapper:hover {
  padding: 0;
}

.iconWrapper:active {
  padding: 2px;
}

.icon {
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.textInput {
  border-radius: 3px;
  padding: 5px 4px;
  border: 1px solid #222;
  color: #000;
  font-size: 12px;
  font-weight: normal;
  width: 200px;
  max-width: 100%;
}

.textInput:focus {
  outline: 0;
  border: 1px solid rgb(20, 189, 175);
  box-shadow: 0 0 3px rgb(20, 189, 175);
}

.goBack {
  margin-left: 10px;
}
