.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.title {
  margin-left: 8px;
  overflow: hidden;
  width: 200px;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 12px;
  color: #000;
}

.iconWrapper {
  margin: 0 1px;
  width: 26px;
  height: 26px;
  padding: 1px;
  flex-shrink: 0;
}

.iconWrapper:hover {
  padding: 0;
}

.iconWrapper:active {
  padding: 2px;
}

.icon {
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.goBack {
  margin-left: 10px;
}
