.button {
  margin: 2px;
  padding: 7px 14px;
  font-size: 12px;
  font-weight: normal;
  text-align: center;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out, border 0.1s ease-in-out;
  box-shadow: 2px 2px 2px #ccc;
  min-width: 80px;
}

.button:focus {
  outline: 0;
}

.button:disabled,
.button:disabled:hover {
  opacity: 0.3;
  cursor: not-allowed;
}

.button:active {
  padding-top: 8px;
  padding-bottom: 6px;
}

.sea,
.sea:disabled:hover {
  color: #fff;
  background-color: rgb(20, 189, 175);
}

.sea:hover {
  color: #fff;
  background-color: #333;
}

.sea:active {
  color: #fff;
  background-color: #222;
}

.grey,
.grey:disabled:hover {
  color: #000;
  background-color: #eee;
}

.grey:hover {
  color: #fff;
  background-color: #333;
}

.grey:active {
  color: #fff;
  background-color: #222;
}

.small {
  padding: 4px 8px;
  min-width: 0;
  font-size: 12px;
  box-shadow: 1px 1px 2px #ccc;
}

.small:active {
  padding-top: 5px;
  padding-bottom: 3px;
}
