.box {
  position: relative;
  background-color: #f0f0f0;
  width: 800px;
  height: 400px;
  max-width: calc(100% - 10px);
  max-height: calc(100% - 10px);
  overflow: hidden;
  padding: 5px;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  box-shadow: 2px 2px 4px #bbb;
}

.outerContainer {
  position: relative;
  padding: 3px;
  height: 100%;
}

.innerContainer {
  width: 100%;
  height: 100%;
  background-color: #fff;
  padding: 3px;
}

.outerLeft {
  width: calc(50% + 1px);
  border-right: 2px solid #ccc;
}

.outerRight {
  width: calc(50% - 1px);
}

.grid {
  width: 100%;
  max-height: 100%;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  flex-direction: row;
  overflow-y: auto;
}

.gridItemWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  padding: 1px;
}

.gridItem {
  width: 100%;
  height: 100%;
  background-color: aqua;
}
