.container {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
  z-index: -1;
}

.checkmark {
  position: relative;
  flex-shrink: 0;
  background-color: #fff;
  border: 1px solid #ccc;
}

.container:hover input ~ .checkmark {
  background-color: #fff;
  border: 1px solid rgb(20, 189, 175);
}

.container input:disabled ~ .checkmark {
  opacity: 0.8;
}

.container:hover input:disabled ~ .checkmark {
  background-color: #fff;
  border: 1px solid #ccc !important;
}

.container input:checked ~ .checkmark {
  background-color: #fff;
  border: 1px solid rgb(20, 189, 175) !important;
}

.container input[type='checkbox'] ~ .checkmark {
  border-radius: 4px;
  width: 18px;
  height: 18px;
}

.container input[type='radio'] ~ .checkmark {
  border-radius: 9.5px;
  width: 19px;
  height: 19px;
}

.checkmark:after {
  content: '';
  position: absolute;
  display: block;
  transition: transform ease 0.15s;
}

.container input[type='checkbox']:checked ~ .checkmark:after {
  display: block;
  -webkit-transform: rotate(45deg) scale(1);
  -ms-transform: rotate(45deg) scale(1);
  transform: rotate(45deg) scale(1);
}

.container input[type='checkbox'] ~ .checkmark:after {
  left: 6px;
  bottom: 2px;
  width: 4px;
  height: 14px;
  border: solid rgb(20, 189, 175);
  border-width: 0px 3.5px 3.5px 0px;
  -webkit-transform: rotate(45deg) scale(0);
  -ms-transform: rotate(45deg) scale(0);
  transform: rotate(45deg) scale(0);
}

.container input[type='radio']:checked ~ .checkmark:after {
  display: block;
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}

.container input[type='radio'] ~ .checkmark:after {
  left: 4.5px;
  bottom: 4.5px;
  width: 8px;
  height: 8px;
  border: 0;
  background-color: rgb(20, 189, 175);
  border-radius: 4px;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
}

.children {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}
