.itemWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 1px;
}

.item {
  width: 100%;
  height: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  cursor: move;
}

.itemDragging {
  /* border: 1px solid aqua; */
  /* transform: rotate(30deg); */
  border: 1px solid rgb(20, 189, 175);
  box-shadow: 0 0 3px rgb(20, 189, 175);
  /* background-color: #ddd; */
  opacity: 0.7;
}

.text {
  font-size: 12px;
  font-weight: normal;
  color: #000;
}
