.box {
  position: relative;
  background-color: #f0f0f0;
  width: 800px;
  height: 400px;
  max-width: calc(100% - 10px);
  max-height: calc(100% - 10px);
  overflow: hidden;
  padding: 5px;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  box-shadow: 2px 2px 4px #bbb;
}

.outerContainer {
  position: relative;
  padding: 3px;
  height: 100%;
}

.innerContainer {
  width: 100%;
  height: 100%;
  background-color: #fff;
  padding: 3px;
  display: flex;
  flex-direction: column;
}

.outerLeft {
  width: calc(50% + 1px);
  border-right: 2px solid #ccc;
}

.outerRight {
  width: calc(50% - 1px);
}

.grid {
  width: 100%;
  max-height: 100%;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  flex-direction: row;
  overflow-y: auto;
}

.gridItemWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  padding: 1px;
}

.gridItem {
  width: 100%;
  height: 100%;
  background-color: rgb(20, 189, 175);
}

.innerContainer {
  width: 100%;
  height: 100%;
  padding: 8px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  transition: background-color 0.25s ease-in-out;
}

.header {
  font-weight: bold;
  font-size: 12px;
  color: #333;
  margin-bottom: 8px;
}

.keyboardControlsBox {
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr;
  margin-left: 10px;
  margin-bottom: 10px;
  align-items: center;
  gap: 2px 6px;
  color: #000;
}

.text {
  font-size: 12px;
  font-weight: normal;
  color: #000;
}

.checkboxText {
  font-size: 12px;
  font-weight: normal;
  color: #000;
  margin-left: 4px;
}

.textInput {
  font-size: 12px;
  font-weight: normal;
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.textInput:focus {
  outline: 0;
  border: 1px solid rgb(20, 189, 175);
  box-shadow: 0 0 3px rgb(20, 189, 175);
}

.displaySettingsBox {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-left: 10px;
}

.checkboxContainer {
  position: relative;
  padding: 4px;
  background: none;
  width: 100%;
}

.checkboxContainer:hover {
  background-color: #ddd;
}

.resetButton {
  margin-top: auto;
  align-self: center;
}
